import { Pizza } from "./pizza1";
import { Pasta } from "./pasta1";
import { Salad } from "./salad1";
import { Drink } from "./drink1";
export class Cart {
  data: (Pizza | Pasta | Salad | Drink)[];
  constructor() {
    this.data = [];
  }
  addToCart(arg: Pizza | Pasta | Salad | Drink) {
    this.data.push(arg);
    this.renderCart();
  }

  getCartData(): (Pizza | Pasta | Salad | Drink)[] {
    return this.data;
  }

  deleteFromCart(id: string) {
    this.data = this.data.filter((pizza) => pizza.id !== id);
    console.log("Hello fromCart", id, this.data);
    this.renderCart();
  }

  renderCart() {
    const container = document.querySelector(".cartData")!;
    container.classList.add("cart_wrapper");
    container.innerHTML = "";

    // // Create an array of <h2> elements using map
    this.getCartData().forEach((str: Pizza | Pasta | Salad | Drink) => {
      if (
        str.order === "PIZZA" ||
        str.order === "PASTA" ||
        str.order === "SALAD"
      ) {
        const cartElement = document.createElement("div");
        cartElement.classList.add("cart_element");

        const h2Element = document.createElement("h4");
        h2Element.textContent = str.base || "Nothing Selected";
        const h6Element = document.createElement("h4");
        h6Element.textContent = str.sauce || "Nothing Selected";
        cartElement?.appendChild(h2Element);
        cartElement?.appendChild(h6Element);
        container.appendChild(cartElement);
      }
      if (str.order === "DRINKS" && Number(str.price) > 0) {
        const cartElement = document.createElement("div");
        cartElement.classList.add("cart_element");

        const h2Element = document.createElement("h2");
        h2Element.textContent = JSON.stringify(str.drinks);
        const h6Element = document.createElement("h6");
        h6Element.textContent = str.price.toString();
        cartElement?.appendChild(h2Element);
        cartElement?.appendChild(h6Element);

        container.appendChild(cartElement);
      }
      // Create <h2> element
      // const h2Element = document.createElement("h2");
      // const h6Element = document.createElement("h6");
      // // Set text content of <h2> element to the string
      // h2Element.textContent = String(str.price) + "$";
      // h6Element.textContent = str.id;

      // // Return the <h2> element
      // return { one: h2Element, two: h6Element };
    });

    // h2Elements.forEach((itm: any) => {
    //   container?.appendChild(itm.one);
    //   container?.appendChild(itm.two);
    // });
  }
}
