* {
  box-sizing: border-box;
  margin: 0;
  text-align: center;
  font-family: "M PLUS Rounded 1c", sans-serif;
  font-weight: 400;
  font-style: normal; }

.hidden {
  display: none; }

.ingredient_container {
  display: flex;
  justify-content: center;
  gap: 5px; }

.container_wrapper {
  display: flex;
  order: 2;
  position: relative;
  justify-content: center;
  align-items: center;
  gap: 40px;
  max-width: 500px;
  margin: 0 auto;
  background-color: aliceblue;
  margin-bottom: 25px; }

.primary__container {
  order: -1;
  display: flex;
  flex-direction: column;
  gap: 25px; }

.deleteOrder {
  position: absolute;
  right: 0;
  top: 0; }

.ingredient_container,
.base,
.sauce {
  margin-top: 10px; }

/* ------ Mobile ------ */
/* ------ Desktop ------ */
header {
  background-color: #556b2f;
  display: grid;
  grid-template-columns: 80px 80% 1fr;
  align-items: center;
  color: white;
  padding-top: 0.5em;
  padding-bottom: 0.5em; }

.logo {
  align-self: center;
  font-size: 50pt;
  width: 100%;
  height: 80px;
  grid-column: 1; }

.btnCartHead {
  grid-column: 3;
  background-color: #556b2f;
  color: white;
  font-size: 25pt;
  border-style: none; }

.btnCartHead:hover {
  cursor: pointer;
  transform: scale(1.5);
  transition: 0.3s ease; }

.heading {
  grid-column: 2;
  text-align: left; }

.menu {
  background-color: #fafafa;
  display: flex;
  flex-direction: column;
  min-height: 100vh; }
  .menu h2 {
    padding: 2em; }
  .menu li {
    list-style-type: none; }
    .menu li a {
      text-decoration: none;
      color: black; }

.displayItem {
  border-radius: 20px;
  background-color: #b6b3b3c8;
  color: #696666;
  margin-left: 2em;
  padding-top: 1em;
  padding-bottom: 1em;
  width: 75%;
  height: fit-content;
  font-size: smaller; }

.pizzaHeading,
.pastaHeading,
.saladHeading,
.drinkHeading {
  display: flex;
  margin-top: 1em;
  flex-direction: row;
  justify-content: center;
  padding-left: 20%;
  padding-right: 20%;
  background-color: darkgoldenrod;
  color: white; }

.ingredientsPizza,
.ingredientsPasta,
.ingredientsSalad {
  padding-top: 1em;
  padding-bottom: 2em;
  padding-left: 20%;
  padding-right: 5%;
  display: grid;
  grid-template-columns: 1fr 1fr; }
  .ingredientsPizza .displayItem,
  .ingredientsPasta .displayItem,
  .ingredientsSalad .displayItem {
    grid-column: 2; }

.base {
  grid-column: 1; }

.IngredList {
  display: grid;
  grid-column: 2fr 1fr; }
  .IngredList ul {
    grid-column: 1; }
  .IngredList li {
    grid-column: 2;
    display: flex;
    justify-content: space-between;
    float: left; }

.btnPizza,
.btnPasta,
.btnSalad,
.btnDrink {
  border: none;
  font-size: larger;
  background-color: transparent;
  color: rgba(252, 252, 252, 0.517);
  padding: none;
  margin-top: -5px; }

.btnPizza:hover,
.btnPasta:hover,
.btnSalad:hover {
  color: white; }

.drinkDisplay {
  display: flex;
  justify-content: center; }
  .drinkDisplay p {
    margin: 1em; }
  .drinkDisplay button {
    margin: 1em;
    height: fit-content; }

footer {
  margin-top: auto;
  background-color: #89938e;
  padding: 1em;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly; }
  footer li {
    list-style-type: none;
    display: inline-block; }
  footer a {
    text-decoration: none;
    color: black;
    padding: 1em; }
